import React, { Component } from 'react';
export default class About extends Component {
  render() {
    let resumeData = this.props.resumeData;
    return (
      <section id="about">
         <div className="row">

            <div className="three columns">

               <img className="profile-pic"  src="images/profilepic.jpg" alt="" />

            </div>

            <div className="nine columns main-col">

               <h2>About Me</h2>
               <p>
               {
                 resumeData.aboutme

               }
               </p>
               <p>
               {
                 resumeData.former

               }
               </p>
               <p><b>
                  {resumeData.Passion}
               </b></p>
               <p>
               <ul>
               <li>{
                 resumeData.i
               }
               </li>
               <li>
               {
                 resumeData.ii
               }
               </li>
               <li>
               {
                 resumeData.iii
               }
               </li>
               </ul>
               </p>
               <p>
               
               </p>
               <p>
                <b>Personal Page : </b>{resumeData.personalPage }
               </p>
               <p><b>GitHub : </b>{resumeData.github}
               </p>
               <p>
               <b>Email ID : </b>{resumeData.email}
               </p>
            </div>
         </div>
      </section>
    );
  }
}
